/* 自定义滚动条 */
/* 彩色滚动条 开始 */
::-webkit-scrollbar {
  width: 10px;
  /*滚动条宽度*/
  height: 30px;
  /*滚动条高度*/
}

/*定义滚动条轨道*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgba(64, 32, 153, 0.719);
  background: #f6f6f6;
}

/*定义滑块*/
::-webkit-scrollbar-thumb {
  background-color: #12b7f5;
  background-image: -webkit-linear-gradient(45deg, rgba(255, 93, 143, 1) 25%, rgb(67, 190, 238) 25%, rgb(67, 190, 238) 50%, rgba(255, 93, 143, 1) 50%, rgba(255, 93, 143, 1) 75%, rgb(67, 190, 238) 75%, rgb(67, 190, 238));
  border-radius: 4px;
}

/* moz样式分隔补加 */
@-moz-document url-prefix() {
  html {
    scrollbar-width: auto;
    scrollbar-color: #13a3db #ffad9e;
  }
}

/* 彩色滚动条 结束*/